
.ul{
    list-style: none;
    display: flex;
    flex-direction: row;
    justify-content: center;
    margin: 0px;
    padding: 0px;
    font-family: 'Pokemon Solid', sans-serif;
    color: white;
    -webkit-text-stroke: 1.5px black;
    gap: 1.5rem;

}
.li{
    display: flex;
    justify-content: center;
    align-content: center;
    flex-direction: row;
    border-radius: 10px;
    width: 25%;
    height:fit-content;
    padding: 0px;
    border: 1px solid rgba(0,0,0,0.3);
    margin-bottom: 40px;
    padding-bottom: 8px;
}
.subrayado{
    text-decoration: none;
    text-shadow: 3px 5px 5px blue;
    -webkit-text-stroke: 1px blue;
    color: yellow;
    font-family: 'Pokemon Solid', sans-serif;
    font-size: 25px;
}

.cardContainer{
    display: flex;
    flex-direction: column;
    justify-content: center;
    margin: 20px;
    /* padding: 3.5rem; */
    width: 330px;
    height: 300px;
    border-radius: 5px;
    align-content: center;
    background-color: rgba(255, 255, 255, .15);  
    backdrop-filter: blur(5px);
    box-shadow: 5px 5px 10px;
    /* gap: 1.5rem; */
}
img{
    image-rendering: auto;
    margin-top: -40px;
}

